import { Component, OnInit, Input } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import { Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para

@Component({
	selector: 'app-hashtag-home',
	templateUrl: './hashtag-home.component.html',
	styleUrls: ['./hashtag-home.component.scss']
})
export class HashtagHomeComponent implements OnInit {

	@Input() input_param;
	list;
	url_part;
	hashtag_id;
	type;

	
	dairy_param;
	pscholoy_param;

	//是否開啟read more div
	readmoreCheck = 0;
	moredata = 1;

	constructor(private httpService:HttpService, protected route:ActivatedRoute) { }

	ngOnInit() {

	}


	//取得hashtag清單
	getList(){

		let params = {
			'hashtag_id': this.hashtag_id,
			'from': this.input_param.from,
			'limit': this.input_param.limit,
		};
		let param = this.httpService.getParamFormat(params);
		let url = `${Config.APIUrl}${this.url_part}?${param}`;
		this.httpService.getHttp(url).subscribe(
			res => {
				//沒有資料 隱藏more data區塊
				if(res.datas.length==0){
					console.log("empty array");
					this.moredata = 0;
				}

				this.list = res.datas;
				console.log(this.list);
			}
		);
	}


	//看更多
	more() {
		this.input_param.from += this.input_param.limit;
		this.readmoreCheck = 1;
		
		//this.getList();		
	}


	selectType(type){

		//limit 設定各種hashtag搜尋結果的筆數
		this.dairy_param = {
			'from':0,
			'limit':2,
		};
		this.pscholoy_param = {
			'from':0,
			'limit':1,
		};

		//顯示區塊
		this.type = type;
	}

}
